import React from 'react'
import "./Content.css"
import { Button } from "antd"
import { thunkDataCreator } from "../../DAL/CountryData"
import { useDispatch } from "react-redux"



export let Content = (props) => {
    const dispatch = useDispatch()
    return (
        <div>
            <div className={"BT"}>
                <Button key={"MainButton"} onClick={() => dispatch(thunkDataCreator())} type={"primary"}>Увидеть страны</Button>
            </div>
                {props.component}
        </div >
    )
}