import React from "react"
import "./Shapka.css"
import {Avatar} from 'antd'
import {UserOutlined} from '@ant-design/icons'

export const Shapka = (props) => {
    return (
        <div className={"ShapkaDIV"}>
            <Avatar className={"Avatar"} size={64} icon={<UserOutlined/>}/>
        </div>
    )
}